'use client';

import { useState } from 'react';
import { CreditCardIcon, BanknotesIcon, WalletIcon } from '@heroicons/react/24/outline';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';

export default function PaymentsPage() {
  const [paymentMethod, setPaymentMethod] = useState('credit-card');
  const [amount, setAmount] = useState('');
  const [cardNumber, setCardNumber] = useState('');
  const [cardName, setCardName] = useState('');
  const [expiryDate, setExpiryDate] = useState('');
  const [cvv, setCvv] = useState('');
  const [showSuccess, setShowSuccess] = useState(false);

  // بيانات المعاملات السابقة
  const transactions = [
    {
      id: 1,
      date: '15 مارس 2025',
      amount: 150,
      description: 'دفع مقابل تصميم صور',
      status: 'مكتمل',
      reference: 'REF123456',
    },
    {
      id: 2,
      date: '10 مارس 2025',
      amount: 250,
      description: 'دفع مقابل إنشاء فيديو',
      status: 'مكتمل',
      reference: 'REF789012',
    },
    {
      id: 3,
      date: '5 مارس 2025',
      amount: 75,
      description: 'دفع مقابل كتابة محتوى',
      status: 'مكتمل',
      reference: 'REF345678',
    },
  ];

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // في تطبيق حقيقي، هنا سيتم إرسال بيانات الدفع إلى بوابة الدفع
    console.log('Processing payment:', {
      paymentMethod,
      amount,
      cardDetails: paymentMethod === 'credit-card' ? { cardNumber, cardName, expiryDate, cvv } : null,
    });
    
    // عرض رسالة النجاح
    setShowSuccess(true);
    
    // إعادة تعيين النموذج
    setTimeout(() => {
      setShowSuccess(false);
      setAmount('');
      setCardNumber('');
      setCardName('');
      setExpiryDate('');
      setCvv('');
    }, 3000);
  };

  return (
    <>
      <Navbar />
      <div className="bg-gray-50 py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-10">
            <h1 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
              المدفوعات
            </h1>
            <p className="mt-4 text-lg text-gray-600">
              إدارة مدفوعاتك وشحن رصيدك لطلب الخدمات
            </p>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* نموذج الدفع */}
            <div className="lg:col-span-2">
              <div className="bg-white shadow rounded-lg p-6">
                <h2 className="text-xl font-semibold text-gray-900 mb-6">شحن الرصيد</h2>
                
                {showSuccess ? (
                  <div className="bg-green-50 border border-green-200 rounded-md p-4 mb-6">
                    <div className="flex">
                      <div className="flex-shrink-0">
                        <svg className="h-5 w-5 text-green-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                        </svg>
                      </div>
                      <div className="mr-3">
                        <p className="text-sm font-medium text-green-800">
                          تمت عملية الدفع بنجاح! سيتم إضافة المبلغ إلى رصيدك قريبًا.
                        </p>
                      </div>
                    </div>
                  </div>
                ) : (
                  <form onSubmit={handleSubmit}>
                    <div className="mb-6">
                      <label htmlFor="amount" className="block text-sm font-medium text-gray-700 mb-2">
                        المبلغ (بالدولار)
                      </label>
                      <input
                        type="number"
                        id="amount"
                        value={amount}
                        onChange={(e) => setAmount(e.target.value)}
                        required
                        className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                        placeholder="أدخل المبلغ"
                      />
                    </div>
                    
                    <div className="mb-6">
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        اختر طريقة الدفع
                      </label>
                      <div className="grid grid-cols-3 gap-3">
                        <div
                          className={`p-3 border rounded-md text-center cursor-pointer ${
                            paymentMethod === 'credit-card'
                              ? 'border-indigo-600 bg-indigo-50 text-indigo-600'
                              : 'border-gray-300 text-gray-700'
                          }`}
                          onClick={() => setPaymentMethod('credit-card')}
                        >
                          <CreditCardIcon className="h-6 w-6 mx-auto" />
                          <span className="text-sm mt-1 block">بطاقة ائتمان</span>
                        </div>
                        <div
                          className={`p-3 border rounded-md text-center cursor-pointer ${
                            paymentMethod === 'bank-transfer'
                              ? 'border-indigo-600 bg-indigo-50 text-indigo-600'
                              : 'border-gray-300 text-gray-700'
                          }`}
                          onClick={() => setPaymentMethod('bank-transfer')}
                        >
                          <BanknotesIcon className="h-6 w-6 mx-auto" />
                          <span className="text-sm mt-1 block">تحويل بنكي</span>
                        </div>
                        <div
                          className={`p-3 border rounded-md text-center cursor-pointer ${
                            paymentMethod === 'e-wallet'
                              ? 'border-indigo-600 bg-indigo-50 text-indigo-600'
                              : 'border-gray-300 text-gray-700'
                          }`}
                          onClick={() => setPaymentMethod('e-wallet')}
                        >
                          <WalletIcon className="h-6 w-6 mx-auto" />
                          <span className="text-sm mt-1 block">محفظة إلكترونية</span>
                        </div>
                      </div>
                    </div>
                    
                    {paymentMethod === 'credit-card' && (
                      <div className="space-y-4">
                        <div>
                          <label htmlFor="card-number" className="block text-sm font-medium text-gray-700 mb-2">
                            رقم البطاقة
                          </label>
                          <input
                            type="text"
                            id="card-number"
                            value={cardNumber}
                            onChange={(e) => setCardNumber(e.target.value)}
                            required
                            maxLength={19}
                            className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                            placeholder="XXXX XXXX XXXX XXXX"
                          />
                        </div>
                        <div>
                          <label htmlFor="card-name" className="block text-sm font-medium text-gray-700 mb-2">
                            الاسم على البطاقة
                          </label>
                          <input
                            type="text"
                            id="card-name"
                            value={cardName}
                            onChange={(e) => setCardName(e.target.value)}
                            required
                            className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                            placeholder="الاسم كما يظهر على البطاقة"
                          />
                        </div>
                        <div className="grid grid-cols-2 gap-4">
                          <div>
                            <label htmlFor="expiry-date" className="block text-sm font-medium text-gray-700 mb-2">
                              تاريخ انتهاء الصلاحية
                            </label>
                            <input
                              type="text"
                              id="expiry-date"
                              value={expiryDate}
                              onChange={(e) => setExpiryDate(e.target.value)}
                              required
                              maxLength={5}
                              className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                              placeholder="MM/YY"
                            />
                          </div>
                          <div>
                            <label htmlFor="cvv" className="block text-sm font-medium text-gray-700 mb-2">
                              رمز الأمان (CVV)
                            </label>
                            <input
                              type="text"
                              id="cvv"
                              value={cvv}
                              onChange={(e) => setCvv(e.target.value)}
                              required
                              maxLength={4}
                              className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                              placeholder="XXX"
                            />
                          </div>
                        </div>
                      </div>
                    )}
                    
                    {paymentMethod === 'bank-transfer' && (
                      <div className="bg-gray-50 p-4 rounded-md border border-gray-200">
                        <h3 className="text-sm font-medium text-gray-700 mb-2">تفاصيل الحساب البنكي:</h3>
                        <p className="text-sm text-gray-600">اسم البنك: بنك عَرَبَاي</p>
                        <p className="text-sm text-gray-600">رقم الحساب: 1234567890</p>
                        <p className="text-sm text-gray-600">IBAN: SA01 2345 6789 1234 5678 9012</p>
                        <p className="text-sm text-gray-600 mt-2">
                          يرجى تحويل المبلغ المذكور إلى الحساب أعلاه وإدخال رقم الحوالة بعد الانتهاء.
                        </p>
                        <input
                          type="text"
                          className="mt-3 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                          placeholder="رقم الحوالة البنكية"
                        />
                      </div>
                    )}
                    
                    {paymentMethod === 'e-wallet' && (
                      <div className="bg-gray-50 p-4 rounded-md border border-gray-200">
                        <h3 className="text-sm font-medium text-gray-700 mb-2">محافظ إلكترونية متاحة:</h3>
                        <div className="space-y-2 mt-3">
                          <div className="flex items-center">
                            <input
                              id="wallet-paypal"
                              name="wallet-option"
                              type="radio"
                              defaultChecked
                              className="h-4 w-4 text-indigo-600 border-gray-300 focus:ring-indigo-500"
                            />
                            <label htmlFor="wallet-paypal" className="mr-3 block text-sm font-medium text-gray-700">
                              PayPal
                            </label>
                          </div>
                          <div className="flex items-center">
                            <input
                              id="wallet-applepay"
                              name="wallet-option"
                              type="radio"
                              className="h-4 w-4 text-indigo-600 border-gray-300 focus:ring-indigo-500"
                            />
                            <label htmlFor="wallet-applepay" className="mr-3 block text-sm font-medium text-gray-700">
                              Apple Pay
                            </label>
                          </div>
                          <div className="flex items-center">
                            <input
                              id="wallet-googlepay"
                              name="wallet-option"
                              type="radio"
                              className="h-4 w-4 text-indigo-600 border-gray-300 focus:ring-indigo-500"
                            />
                            <label htmlFor="wallet-googlepay" className="mr-3 block text-sm font-medium text-gray-700">
                              Google Pay
                            </label>
                          </div>
                        </div>
                        <p className="text-sm text-gray-600 mt-4">
                          سيتم تحويلك إلى صفحة المحفظة المختارة لإتمام عملية الدفع.
                        </p>
                      </div>
                    )}
                    
                    <div className="mt-6">
                      <button
                        type="submit"
                        className="w-full inline-flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                      >
                        إتمام الدفع
                      </button>
                    </div>
                  </form>
                )}
              </div>
            </div>
            
            {/* ملخص الرصيد والمعاملات */}
            <div className="lg:col-span-1">
              <div className="bg-white shadow rounded-lg p-6 mb-6">
                <h2 className="text-xl font-semibold text-gray-900 mb-4">ملخص الرصيد</h2>
                <div className="mt-2 flex items-center justify-between">
                  <span className="text-gray-500">الرصيد الحالي:</span>
                  <span className="text-xl font-bold text-gray-900">$500</span>
                </div>
                <div className="mt-2 flex items-center justify-between">
                  <span className="text-gray-500">معلق:</span>
                  <span className="text-lg text-gray-700">$75</span>
                </div>
                <div className="mt-2 flex items-center justify-between">
                  <span className="text-gray-500">متاح للسحب:</span>
                  <span className="text-lg text-gray-700">$425</span>
                </div>
                <div className="mt-6">
                  <button
                    type="button"
                    className="w-full inline-flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                  >
                    طلب سحب الرصيد
                  </button>
                </div>
              </div>
              
              <div className="bg-white shadow rounded-lg p-6">
                <h2 className="text-xl font-semibold text-gray-900 mb-4">آخر المعاملات</h2>
                <div className="divide-y divide-gray-200">
                  {transactions.map((transaction) => (
                    <div key={transaction.id} className="py-3">
                      <div className="flex justify-between">
                        <span className="text-sm font-medium text-gray-900">{transaction.description}</span>
                        <span className="text-sm font-bold text-gray-900">${transaction.amount}</span>
                      </div>
                      <div className="flex justify-between mt-1">
                        <span className="text-xs text-gray-500">{transaction.date}</span>
                        <span className="text-xs px-2 inline-flex leading-5 font-semibold rounded-full bg-green-100 text-green-800">
                          {transaction.status}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
                <div className="mt-6">
                  <button
                    type="button"
                    className="w-full inline-flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                  >
                    عرض جميع المعاملات
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
} 